// https://gallery.echartsjs.com/editor.html?c=xB1XmPH9jb
export const renderSkillKeywordsChart = (container) => {
  const chart = echarts.init(container);

  const data = [];
  const links = [];
  const categories = [];

  for (let i = 0; i < SKILLS.length; i++) {
    const current = SKILLS[i];
    const target = {
      name: current.name,
      category: current.category,
      x: current.x || 0,
      y: current.y || 0,
    };

    const link = {
      source: current.name,
      target: current.category,
    };

    if (!current.category) {
      if (categories.length) {
        link.target = categories[categories.length - 1].name;
        link.lineStyle = {
          color: "rgba(128, 128, 128, 0.3",
        };
      }

      target.symbolSize = 12;

      categories.push({ name: current.name });
    }

    data.push(target);
    links.push(link);
  }

  const options = {
    animationEasingUpdate: "quinticInOut",
    series: [
      {
        name: "",
        type: "graph",
        symbolSize: 10,
        data,
        links,
        categories,
        emphasis: {
          focus: "adjacency",
        },
        label: {
          show: true,
          position: "top",
        },
      },
    ],
  };

  chart.setOption(options);

  return chart;
};
