import * as echarts from "echarts/core";
import {
  MapChart,
  MapSeriesOption,
  GraphChart,
  GraphSeriesOption,
  ScatterChart,
  ScatterSeriesOption,
} from "echarts/charts";
import {
  TitleComponent,
  TitleComponentOption,
  GridComponent,
  GridComponentOption,
  TooltipComponent,
  TooltipComponentOption,
  VisualMapComponent,
  VisualMapComponentOption,
} from "echarts/components";
import { CanvasRenderer } from "echarts/renderers";

//  ComposeOption  Option 
export type ECOption = echarts.ComposeOption<
  | MapSeriesOption
  | GraphSeriesOption
  | TitleComponentOption
  | GridComponentOption
  | TooltipComponentOption
  | ScatterSeriesOption
  | VisualMapComponentOption
>;

// 
echarts.use([
  CanvasRenderer,
  TitleComponent,
  TooltipComponent,
  GridComponent,
  MapChart,
  GraphChart,
  ScatterChart,
  VisualMapComponent,
]);

export default echarts;
